$tablet-width: 768px;
$desktop-width: 800px;
$smartphone-width: 240px;

@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content; } }

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content; } }

@mixin smartphone {
  @media (min-width: #{$smartphone-width}) and (max-width: #{$tablet-width - 1px}) {
    @content; } }

@mixin break-between($min, $max) {
  @media (min-width: $min) and (max-width: $max) {
    @content; } }

@mixin break-min($min) {
  @media (min-width: $min) {
    @content; } }

@mixin break-max($max) {
  @media (max-width: $max) {
    @content; } }

// font family mixins
@function default-font($size : '') {
  @if $size == 100 {
     @return 'museo_sans_rounded100', sans-serif; }
  @else if $size == 300 {
     @return 'museo_sans_rounded300', sans-serif; }
  @else if $size == 500 {
     @return 'museo_sans_rounded500', sans-serif; }
  @else if $size == 700 {
     @return 'museo_sans_rounded700', sans-serif; }
  @else if $size == 900 {
     @return 'museo_sans_rounded900', sans-serif; }
  @else {
     @return 'museo_sans_rounded500', sans-serif; } }
