.sign-me-up {
  margin: 30px auto;
  text-align: center;
  &.only-mobile {
    margin-bottom: 0; }

  h1 {
    margin: 0 auto 40px;
    text-align: center;
    font-family: default-font(500);
    max-width: 600px;
    color: $c0;
    @include break-min(769px) {
      font-size: rem(30px); }
    @include break-max(768px) {
      font-size: rem(22px); } }

  img {
    width: 100%;
    height: auto; }

  .sign-me-up-btn {
    margin: 0 auto;
    display: table;
    border-radius: 60px;
    padding: 10px;
    border: 1px solid rgba($c0, 0);
    transition: 0.4s $enter background;
    text-decoration: none;


    &:hover {
      border-color: rgba($c0, 0.2);
      background: rgba($c0, 0.2); }

    span {
      display: block;
      padding: 10px 50px;
      border: 0;
      border-radius: 60px;
      background: $c0;
      color: $c7;
      font-family: default-font(300);
      text-transform: uppercase;
      line-height: 1em;
      @include break-min(769px) {
        font-size: rem(36px); }
      @include break-max(768px) {
        font-size: rem(20px); } } } }
